        this.enteredChallanNo = $( "#challanNo" ).val();
        this.lrtDtoForChallan = new LRDto();
        this.lrtDtoForChallan.challanNo = this.enteredChallanNo;
        this.lrtDtoForChallan.companyId = this.userDataDtoReturnSession.companyId;
