    setPrintLrs( resultDtos ) {

        swal( {
            title: "Validation",
            text: "Sure You Want To Take All 3 Copies Print Out Of LR No:" + resultDtos.lrNumber,
            icon: "warning",
            closeOnClickOutside: false,
            closeOnEsc: false,
            buttons: ["No", "Yes"],
        } ).then(( printLr ) => {
            if ( printLr ) {
                //Pending 
                if ( this.stateByDest == 'Kerala' ) {
                    //Pending 
                    //                new LrAutoAllPrintOut(resultDtos1, "ALL", "KL",
                    //                        "original");
                    this.resultDtos.lrEntryCopyType = 'ALL';
                    this.resultDtos.lrEntryMode = 'KL';
                    this.resultDtos.lrEntryPrintMode = 'original';
                    this.lrAutoAllPrintOut( this.resultDtos );

                } else {
                    //                    alert( 'JJJ' );
                    this.resultDtos.lrEntryCopyType = 'ALL';
                    this.resultDtos.lrEntryMode = 'Others';
                    this.resultDtos.lrEntryPrintMode = 'original';
                    this.lrAutoAllPrintOut( this.resultDtos );
                    //Pending 
                    //                new LrAutoAllPrintOut(resultDtos1, "ALL",
                    //                        "Others", "original");

                }
            } else {
                this.isPaidLr = false;
                this.selectedDestination = null;
                //                $( "#destination" ).val( '' );
                window.setTimeout( function() {
                    $( "#destination" ).focus();
                }, 100 );
                this.clearInputFields();


                if ( this.viewAutoLrHold == true ) {
                    this.getAutomatedLrHoldDetails();
                } else {
                    this.getAutoLrNo();
                }

            }
        } );
    }
    lrAutoAllPrintOut( resultDtos ) {
        //      this.getUserValues();
        localStorage.clear();
        //        console.log( this.resultDtos );
        localStorage.setItem( 'printAfterSaveForLrEntry', JSON.stringify( this.resultDtos ) );
        this.viewLrEntryPrint = true;
        this.lrEntryForm = false;
        window.addEventListener( 'afterprint', ( onclick ) => {
            if ( this.viewLrEntryPrint ) {
                this.viewLrEntryPrint = false;
                this.lrEntryForm = true;
                window.setTimeout( function() {
                    $( "#destination" ).focus();
                }, 100 );
                this.clearInputFields();
                if ( this.viewAutoLrHold == true ) {
                    //   console.log( 'aaa1212' );
                    this.getAutomatedLrHoldDetails();
                    window.setTimeout( function() {
                        $( "#automatedLRHold" ).prop( "checked", true );

                    }, 1000 );
                    //    console.log( this.autoLrLoaderHead, this.viewByAutoLr );
                    setTimeout(() => {
                        this.setLoaderHeadVal();
                    },
                        5000 );
                } else {

                    //   console.log( 'aaa12121' );
                    this.getAutoLrNo();
                    window.setTimeout( function() {
                        $( "#automatedLR" ).prop( "checked", true );
                    }, 1000 );
                    //    console.log( this.autoLrLoaderHead, this.viewByAutoLr );
                    setTimeout(() => {
                        this.setLoaderHeadVal();
                    },
                        5000 );


                }

            }
        } );


        //        console.log( localStorage.getItem( 'print' ) );
        //        $( "#lrNumber" ).focus();
    }
